.Analytics {
    overflow-y: scroll;
    height: calc(100vh - 56px);
    background: #F7F7F7;

    .horizontalScroll {
        display: flex;
        overflow: hidden;
        column-gap: 40px;
    }

    .innerDivPadding {
        padding: 0 16px 16px 16px;
        background-color: #F7F7F7;
        height: calc(100vh - 50px);
        padding-top: 30px;
    }

    .chartSection {
        margin-top: 40px;
        background: #FFF;
        height: 250px;
        padding: 22px 16px 16px 16px;
        border: 1px solid #49607326;
    }

    .CampaignSection {
        margin-top: 32px;
    }
}